<template>
  <div v-if="showBlock" class="block segment" @click="stopTimer">
    <h2>Click me {{reactionTime}}</h2>
  </div>
</template>

<script>
export default {
  props: ["delay"],
  data() {
    return {
      showBlock: false,
      timer: null,
      reactionTime: 0,

      
    };
  },

  mounted() {
this.random_height = Math.random()*68+'%'
this.random_width = Math.random()*68+'%'

    // console.log(this.delay);
    setTimeout(() => {
      this.startTimer();
      this.showBlock = true;
    }, this.delay);
  },

  methods:{
      startTimer(){
        
          this.timer = setInterval(() => {
            this.reactionTime +=5;
            // this.$emit('currentReactionTime', this.reactionTime);

          }, 5)
      },
      stopTimer(){
          clearInterval(this.timer);
          this.$emit('end', this.reactionTime);
        //   console.log(this.reactionTime);
      },


  },

  computed: {
    red_() {
        return Math.round(this.reactionTime/10);
    },
    green_() {
        return Math.round(255-this.reactionTime/10);
    },
          
},

//   updated() {
//     console.log("upd");
//   },


};


</script>

<style>
.block {
  position:absolute;
  width: 12em;
  height: 7em;
  border-radius: 16px;
  /* background: #0faf87; */
  background: rgb(v-bind(red_),v-bind(green_),0);
  color: #fff;
  text-align: center;
  padding-top: 2.2em;
  /* margin: v-bind(random_height) v-bind(random_width); */
  bottom:v-bind(random_height);
  left:v-bind(random_width);

  cursor: crosshair;

}
</style>